body {
  background-image: url(https://images.pexels.com/photos/281260/pexels-photo-281260.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  color: rgba(255, 255, 255, 0.904);
  margin: 0;
  font-family: -apple-system, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 30px;
  background-color: rgb(164, 236, 236);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  color: rgba(255, 255, 255, 0.904);
}

.Pregunta {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin-top: 250px;
}
.button {
  font-size: 25px;
  border: 3px white solid;
  border-radius: 10px;
  width: 350px;
  height: 60px;
  text-align: center;
  margin: 8px;
  background-color: transparent;
  font-weight: bold;
  transition: 0.2s;
}
.button:hover {
  background-color: whitesmoke;
  color: black;
  font-weight: bolder;
}
.FinalScore {
  font-size: 80px;
  margin-top: 200px;
}
.ReviewAnswer {
  border-radius: 40px;
  padding: 0;
  background-color: rgb(18, 6, 110);
  margin: 10px;
  display: inline-block;
  width: 50%;
}
@media (max-width: 1041px) {
  .FinalScore {
    font-size: 50px;
    margin-top: 100px;
  }
  .ReviewAnswer {
    width: 90%;
  }
}

.rQuestion {
  border-radius: 40px;
  background-color: rgb(33, 20, 46);
  padding: 20px;
  margin: 10px;
}
.rAnswer {
  border-radius: 40px;
  background-color: rgb(198, 181, 214);
  padding: 20px;
  margin: 20px;
}
.IncorrectAns {
  border-radius: 40px;
  background-color: rgb(177, 16, 78);
  margin-top: 0;
}
.CorrectAns {
  border-radius: 40px;
  background-color: rgb(18, 175, 52);
}
.Incorrectly {
  border-radius: 40px;
  font-size: 25px;
  padding: 10px;
  background-color: rgb(95, 3, 38);
  margin: 20px;
}
.QuestionNumber {
  font-weight: lighter;
  color: rgb(0, 0, 0);
  position: fixed;
  bottom: 0;
  font-size: 20px;
}

